const BASE_URL = 'http://horse-activity.chebaba.com'
export default {

    /**
     * [getParam ]
     * @param  {String} name
     * @param  {String} url   [default:location.href]
     * @return {String|Boolean}
     */
    getParam(name, url) {
        if (typeof name !== 'string') return false
        if (!url) url = window.location.href
        // 当遇到name[xx]时，对方括号做一下转义为 name\[xxx\]，因为下面还需要使用name做正则
        name = name.replace(/[[\]]/g, '\\$&')
        var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)')
        var results = regex.exec(url)
        if (!results) return null
        if (!results[2]) return ''
        return decodeURIComponent(results[2].replace(/\+/g, ' '))
    },

    /**
     * [clone ] 适用于简单object数据，会忽略Function
     * @param  {Object} obj
     * @return {Object}
     */
    clone(obj = {}) {
        return JSON.parse(JSON.stringify(obj))
    },

    /**
     * [getDay ] 获取周几
     * @param  {String} date_string '2018-10-10'
     * @return {Number} 0 (0 => '周日')
     */
    getDay(date_string) {
        return new Date(date_string).getDay()
    },

    /**
     * [setStorage ] 设置localstorage
     * @param  {String} key
     * @param  {Any} val
     * @return undefined
     */
    setStorage(key, val) {
        let val_str = {val}
        val_str = JSON.stringify(val_str)
        key = key.toString()

        window.localStorage.setItem(key, val_str)
    },

    /**
     * [getStorage ] 获取localstorage
     * @param  {String} key
     * @return {value}
     */
    getStorage(key) {
        key = key.toString()
        let val = window.localStorage.getItem(key)

        if (val) {
            val = JSON.parse(val)
            val = val.val
        }

        return val
    },

    /**
     * [removeStorage ] 移除localstorage
     * @param  {String} key
     * @return undefined
     */
    removeStorage(key) {
        key = key.toString()
        window.localStorage.removeItem(key)
    },

    /**
     * [clearStorage ] 清除localstorage
     * @return undefined
     */
    clearStorage() {
        window.localStorage.clear()
    }
}